/* global BigInt */

import NFTCard from "./NFTCard.jsx";
import {
  Container,
  Overlay,
  Row,
  Button,
  Form,
  Tooltip,
} from "react-bootstrap";
import React from "react";
import CustomDivider from "./CustomDivider";
import Sha256 from "crypto-js/sha256";
import Hex from "crypto-js/enc-hex";
import {
  getBusy,
  getContent,
  getBuyTransaction,
  sendBuyTransaction,
} from "./api";
import { getMeta, getState, getProfile } from "./storage";
import { toSatoshi, satoshiToLocaleString } from "./utils";
import GetWalletModal from "./GetWalletModal";
import {
  TwitterShareButton,
  TwitterIcon,
  RedditShareButton,
  RedditIcon,
  FacebookShareButton,
  FacebookIcon,
  LinkedinShareButton,
  LinkedinIcon,
} from "react-share";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLink, faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";

function NFTController() {
  const [address, setAddress] = React.useState(null);
  const [shareLink, setShareLink] = React.useState("");
  const [meta, setMeta] = React.useState(null);
  const [state, setState] = React.useState(null);
  const [ownerProfile, setOwnerProfile] = React.useState(null);
  const [creatorProfile, setCreatorProfile] = React.useState(null);

  const [busy, setBusy] = React.useState(undefined);
  const [resellPrice, setResellPrice] = React.useState("");
  const [resellPriceError, setResellPriceError] = React.useState("");
  const [buyResult, setBuyResult] = React.useState(null);

  const [getWalletShow, setGetWalletShow] = React.useState(false);

  const shareLinkTarget = React.useRef(null);
  const [showCopied, setShowCopied] = React.useState(false);

  const doCopyLink = (e) => {
    e.preventDefault();
    e.stopPropagation();

    navigator.clipboard.writeText(shareLink);

    setShowCopied(true);
    setTimeout(() => setShowCopied(false), 1000);
  };

  React.useEffect(() => {
    const a = new URLSearchParams(window.location.search).get("address");
    setAddress(a);
    setShareLink(`https://nftlink.dingocoin.org/nft?address=${a}`);
  }, []);

  React.useEffect(() => {
    if (resellPrice === "") {
      setResellPriceError("Resell price required");
    } else if (state !== null) {
      if (BigInt(toSatoshi(resellPrice)) > BigInt(10) * BigInt(state.stats.price)) {
        setResellPriceError(
          `Resell price must be at most 10x the current price`
        );
      } else {
        setResellPriceError("");
      }
    } else {
      setResellPriceError("");
    }
  }, [resellPrice, state]);

  React.useEffect(() => {
    document.title = `NFT | ${address}`;
    if (address !== null) {
      (async () => {
        const m = await getMeta(address);
        setMeta(m);
        const s = await getState(address);
        setState(s);

        if (s !== null) {
          document.title = `NFT | ${m.name}`;
          setOwnerProfile(await getProfile(s.owner));
          setCreatorProfile(await getProfile(s.creator));
        }

        setBusy(await getBusy({ address: address }));
      })();
    }
  }, [address]);

  const doDownload = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    if (window.dingo === undefined) {
      setGetWalletShow(true);
      return;
    }

    const timestamp = Date.now();
    const message = `${address}|${timestamp}`;
    const signResult = await window.dingo.requestSign(
      Hex.stringify(Sha256(message))
    );
    if (signResult.error !== undefined) {
      return;
    }

    const result = await getContent({
      address: address,
      timestamp: timestamp,
      signature: signResult.result,
    });
    if (result === null || result.error !== undefined) {
      return;
    }

    if (result.content !== undefined) {
      window.open(result.content);
    }
  };

  const doBuy = async (e) => {
    e.preventDefault();
    e.stopPropagation();

    if (window.dingo === undefined) {
      setGetWalletShow(true);
      return;
    }

    const { vins, vouts } = await getBuyTransaction({
      address: address,
      price: toSatoshi(resellPrice),
    });
    const signed = (await window.dingo.requestSignTransaction(vins, vouts))
      .result;
    if (signed === null || signed === undefined) {
      return;
    }

    const result = await sendBuyTransaction({ transaction: signed });
    setBuyResult(result);
  };

  return (
    <div>
      <Container className="justify-content-md-center">
        <Row>
          <div className="cards-container">
            <NFTCard address={address} />
          </div>
          {meta !== null &&
            meta.description !== null &&
            meta.description !== "" && (
              <div>
                <p className="text-center mt-4">
                  <i>{meta.description}</i>
                </p>
                <div className="text-center">
                  <span className="mb-1" style={{ display: "block" }}>
                    Share <FontAwesomeIcon icon={faExternalLinkAlt} />
                  </span>
                  <Button
                    className="share-link-button"
                    ref={shareLinkTarget}
                    onClick={doCopyLink}
                  >
                    <svg
                      viewBox="0 0 64 64"
                      width="32"
                      height="32"
                      style={{ position: "absolute", top: 0, left: 0 }}
                    >
                      <circle cx="32" cy="32" r="31" fill="#6785ff"></circle>
                    </svg>
                    <FontAwesomeIcon
                      style={{
                        width: "16px",
                        height: "16px",
                        position: "absolute",
                        top: 0,
                        left: 0,
                        margin: "8px",
                        color: "white",
                      }}
                      icon={faLink}
                    ></FontAwesomeIcon>
                  </Button>
                  <Overlay
                    target={shareLinkTarget.current}
                    show={showCopied}
                    placement="top"
                  >
                    {(props) => (
                      <Tooltip {...props}>Link copied to clipboard!</Tooltip>
                    )}
                  </Overlay>
                  <TwitterShareButton
                    url={shareLink}
                    className="mx-2"
                    title="Check out my cool NFT on Dingocoin's NFT Platform!"
                    hashtags={["dingocoin", "nft"]}
                  >
                    <TwitterIcon size={32} round={true} />
                  </TwitterShareButton>
                  <RedditShareButton
                    url={shareLink}
                    title="Check out my cool NFT on Dingocoin's NFT Platform!"
                  >
                    <RedditIcon size={32} round={true} />
                  </RedditShareButton>
                  <FacebookShareButton
                    url={shareLink}
                    className="mx-2"
                    quote="Check out my cool NFT on Dingocoin's NFT Platform!"
                    hashtags="#dingocoin #nft"
                  >
                    <FacebookIcon size={32} round={true} />
                  </FacebookShareButton>
                  <LinkedinShareButton
                    url={shareLink}
                    title="Check out my cool NFT on Dingocoin's NFT Platform!"
                    summary="Check out my cool NFT on Dingocoin's NFT Platform!"
                  >
                    <LinkedinIcon size={32} round={true} />
                  </LinkedinShareButton>
                </div>
              </div>
            )}
        </Row>
        <CustomDivider />
        <Row className="text-center">
          <h3>Owner</h3>
          <p>
            <a
              className="simple-link"
              href={state === null ? "" : `/profile?address=${state.owner}`}
              style={{ fontSize: "0.9rem" }}
            >
              {ownerProfile === null ||
              ownerProfile.name === null ||
              ownerProfile.name === ""
                ? state === null
                  ? "-"
                  : state.owner
                : ownerProfile.name}
            </a>
          </p>
          <h3 className="mt-2">Creator</h3>
          <a
            className="simple-link"
            href={state === null ? "" : `/profile?address=${state.creator}`}
            style={{ fontSize: "0.9rem" }}
          >
            {creatorProfile === null ||
            creatorProfile.name === null ||
            creatorProfile.name === ""
              ? state === null
                ? "-"
                : state.creator
              : creatorProfile.name}
          </a>
          {state !== undefined && state !== null && (
            <div>
              <h3 className="mt-4">Hidden Content</h3>

              <div
                className="clickable mb-2"
                onClick={doDownload}
                style={{ display: "inline" }}
              >
                (Verify and view)
              </div>
            </div>
          )}
        </Row>
        <CustomDivider />
        {busy !== undefined && busy !== null && (
          <Row className="justify-content-md-center mt-2 text-center">
            <h2>Buy NFT</h2>
            <div style={{ textAlign: "center" }} className="mt-4">
              {" "}
              <h5>NFT busy</h5>
              <p>
                <a
                  className="simple-link"
                  href={
                    "https://openchains.info/coin/dingocoin/tx/" + busy.txid
                  }
                  rel="noreferrer"
                  target="_blank"
                >
                  A transaction
                </a>{" "}
                is currenly being confirmed for this NFT. <br />
                Please try again in a few minutes.
              </p>
            </div>
          </Row>
        )}
        {busy !== undefined && busy === null && (
          <Row className="justify-content-md-center mt-2 text-center">
            <h2>Buy NFT</h2>
            {buyResult == null && (
              <Container className="justify-content-md-center">
                <Row className="mx-auto" style={{ maxWidth: "35rem" }}>
                  <Form.Group className="mb-3 mt-3">
                    <Form.Label>
                      <b>Resell at (*required)</b>
                    </Form.Label>
                    <Form.Control
                      type="text"
                      value={resellPrice}
                      onChange={(e) => {
                        if (e.target.value.match(/^[0-9]*$/)) {
                          setResellPrice(e.target.value);
                        }
                      }}
                      style={{ textAlign: "center" }}
                      isInvalid={resellPriceError !== ""}
                    />
                    {resellPriceError && (
                      <div>
                        <Form.Text className="input-error">
                          {resellPriceError}
                        </Form.Text>
                      </div>
                    )}
                    <Form.Text className="text-muted">
                      New price you want to sell this NFT for (min. 1
                      Dingocoin).
                    </Form.Text>
                  </Form.Group>
                </Row>
                <Row>
                  <Button
                    className="popup-button mb-4 mx-auto px-4"
                    style={{ width: "auto" }}
                    onClick={doBuy}
                    disabled={resellPriceError !== ""}
                  >
                    Buy and resell NFT
                  </Button>
                </Row>
              </Container>
            )}
            {buyResult !== null && buyResult.error === undefined && (
              <Container>
                <div style={{ textAlign: "center" }} className="mt-4">
                  {" "}
                  <h4>Transaction submitted!</h4>
                  <p>
                    Transaction ID:{" "}
                    <a
                      className="simple-link"
                      href={
                        "https://openchains.info/coin/dingocoin/tx/" +
                        buyResult.txid
                      }
                      rel="noreferrer"
                      target="_blank"
                    >
                      {buyResult.txid}
                    </a>
                    <br />
                    It may take a few minutes for your transaction to be
                    confirmed.
                  </p>
                </div>
              </Container>
            )}
            {buyResult !== null && buyResult.error !== undefined && (
              <Container>
                <div style={{ textAlign: "center" }} className="mt-4">
                  {" "}
                  <h4>Transaction failed!</h4>
                  <p>
                    - Make sure that your wallet has no pending transaction.
                    <br />- You can't buy this NFT as its owner/creator.
                  </p>
                </div>
              </Container>
            )}
          </Row>
        )}
      </Container>

      <GetWalletModal
        show={getWalletShow}
        onHide={() => setGetWalletShow(false)}
      />
    </div>
  );
}

export default NFTController;
