import { Container, Row, Dropdown } from "react-bootstrap";
import React from "react";
import CustomDivider from "./CustomDivider";
import NFTCard from "./NFTCard";
import { queryNft } from "./api";

function ExploreController() {
  const [nftList, setNftList] = React.useState(null);
  const [queryText, setQueryText] = React.useState("Newly listed");

  React.useEffect(() => {
    document.title = `Explore NFTs`;
    (async () => {
      const l = await queryNft({
        key: "listHeight",
        direction: "DESC",
        limit: 100,
        offset: 0,
      });
      setNftList(l.results);
    })();
  }, []);

  const doChangeQuery = async (eventKey, event) => {
    const [key, direction] = eventKey.split('|');
    setQueryText(event.target.innerText);
    const l = await queryNft({
      key: key,
      direction: direction,
      limit: 100,
      offset: 0,
    });
    setNftList(l.results);
  };

  return (
    <div>
      <Container className="App-header">
        <Row>
          <h1 className="mt-4">Explore NFTs</h1>
          <CustomDivider />
        </Row>
        <Row>
          <Dropdown onSelect={doChangeQuery} >
            <Dropdown.Toggle className="px-4">
              {queryText}
            </Dropdown.Toggle>

            <Dropdown.Menu>
              <Dropdown.Item eventKey="listHeight|DESC">Newly listed</Dropdown.Item>
              <Dropdown.Item eventKey="tradeHeight|DESC">Recently sold</Dropdown.Item>
              <Dropdown.Item eventKey="price|DESC">Highest price</Dropdown.Item>
              <Dropdown.Item eventKey="price|ASC">Lowest price</Dropdown.Item>
              <Dropdown.Item eventKey="tradeCount|DESC">Highest trade count</Dropdown.Item>
              <Dropdown.Item eventKey="tradeCount|ASC">Lowest trade count</Dropdown.Item>
              <Dropdown.Item eventKey="tradeVolume|DESC">Highest trade volume</Dropdown.Item>
              <Dropdown.Item eventKey="tradeVolume|ASC">Lowest trade volume</Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </Row>
      </Container>

      {nftList !== null && (
        <ul className="cards-container">
          {nftList.map((x) => (
            <a href={`/nft?address=${x}`}>
              <NFTCard key={x} address={x} />
            </a>
          ))}
        </ul>
      )}
    </div>
  );
}

export default ExploreController;
